import bottomModal from '../bottomModal/bottom-modal.vue'
export default {
	props: {
		isShowModal: {
			type: Boolean
		},
		tips: {
			type: String,
			default() {
				return '实名认证'
			}
		}
	},
	components: {
		bottomModal
	},
	methods: {
		onGetUserInfo(event) {
			console.log(event)
			const userInfo = event.detail.userInfo
			if(userInfo) {
				this.onbottomModalclosed()
				this.$emit('successLogin', userInfo)
			}else {
				this.$emit('failLogin')
			}
		},
		onbottomModalclosed() {
			this.$emit('bottomModalclosed')
		}
	}
}
